import { Component , OnInit , ViewChild } from '@angular/core';
import {AlertController, ModalController ,  IonRouterOutlet} from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import {  Router, NavigationExtras } from '@angular/router';
import { AuthenticationService } from '../../services/Authentication.service';
import { FormGroup, FormControl } from '@angular/forms';
import { ReplaySubject} from 'rxjs';
import { IonInfiniteScroll } from '@ionic/angular';
import { PermisoCreatePage } from './permiso-create/permiso-create.page';
@Component({
selector: 'page-permiso-list',
templateUrl: 'permiso-list.page.html',
styleUrls: ['./permiso-list.page.scss'],
})
export class PermisoListPage implements OnInit {
@ViewChild(IonInfiniteScroll, { static : false }) infiniteScroll: IonInfiniteScroll;
rangeForm: FormGroup;
searchQuery: string;
showAgeFilter = false;
showmenu = true;
items: any  = [] ;
variables: any = [];
userinfo: any = [];
idllave:any;
viewType = 'grid';
tituloxx = '';
efecto = 0;
seguro ='';
msg = '';
apifoto: String;
itemsList: Array<any>;
public currentSegment: string = "Todos";
searchSubject: ReplaySubject<any> = new ReplaySubject<any>(1);
constructor(  private routerOutlet: IonRouterOutlet , private router: Router, private authenticationService: AuthenticationService,
   public alertController: AlertController,     public translateService: TranslateService    ,    private modalController: ModalController
   ) {  this.apifoto = this.authenticationService.getapifoto();
 }
changeToList() { this.viewType = 'list'; }
changeToGrid() {   this.viewType = 'grid';  }
ngOnInit() {
    this.variables = this.router.getCurrentNavigation().extras.state.variables;
    this.idllave = this.router.getCurrentNavigation().extras.state.idllave;
    this.userinfo = this.authenticationService.getcopiauser();
    this.tituloxx = this.router.getCurrentNavigation().extras.state.title ;
    this.searchQuery = '';
    this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value  });
    this.translateService.get('MSGM').subscribe((value) => {   this.msg = value  });
    this.rangeForm = new FormGroup({    dual: new FormControl({lower: 1, upper: 100})   });   
    this.currentSegment = 'todos';
    this.pideregistros( this.currentSegment);
  }
  filterContacts(event: any) {
    let selectedCategory = event.detail.value;
    this.currentSegment = selectedCategory;
    this.pideregistros(selectedCategory);
  }
  pideregistros0() {
   this.pideregistros(this.currentSegment);
  }
  pideregistros(op) {
       this.itemsList = [];
       this.efecto = 0 ;
      //   this.regService.getReg(this.idrol +'/' + this.idempresa + '/todos','permisos').then((data) => {
       this.authenticationService.getReg( this.idllave + '/' + op + '/' + this.userinfo[0].idempresa._id, 'permisos', 0).then((data) => {
       this.efecto=1;
       this.items =  data;
       this.itemsList = this.items;
 }, (err) => {
   this.authenticationService.presentAlert(err.error , this.msg , '');
 });
  }
  searchList() {
    this.searchSubject.next({
     lower: this.rangeForm.controls.dual.value.lower,
     upper: this.rangeForm.controls.dual.value.upper,
     query: this.searchQuery
   });
    const query = (this.searchQuery && this.searchQuery !== null) ? this.searchQuery : '';
    this.itemsList = this.filterList(this.items, query);
  }
 filterList(list, query): Array<any> {
   return list.filter(item => item.nombre.toLowerCase().includes(query.toLowerCase()));
  }
  async otrasop(item: any) { 
    const alert = await this.alertController.create({
      cssClass: 'my-custom-class',
     header: 'Seleccione Opción',
     inputs: [
       {
         name: 'SubMenus',
         type: 'radio',
         label: 'SubMenus',
         checked: true,
         value: '/Permiso2ListPage°SubMenus ->' + item.nombre,
       }
     ],
     buttons: [
       {
         text: 'Cancel',
         role: 'cancel',
         cssClass: 'secondary',
         handler: (todo) => {
           console.log(todo);
         }
       }, {
         text: 'Ok',
         handler: (todo) => {
           var arr = todo.split('°');
           const navigationExtras: NavigationExtras = {
             state: {
               variables: this.variables,
               user: null,
               idllave: item._id,
               idrol:this.idllave,
               idempresa: this.userinfo[0].idempresa._id,
               title: arr[1]
             }
           };
           this.router.navigate([arr[0]], navigationExtras);
         }
       }
     ]
   });
   await alert.present();   
   
 }
   async deleteRecord(item: any) {
    const alert = await this.alertController.create({
      header: this.seguro,
      message: '',
      buttons: [
        {
          text: 'No',
         cssClass: 'secondary',
          handler: (blah) => {
          }
        }, {
           text: 'Si',
           handler: () => {
             const recordID: string		= item._id;
             this.authenticationService.deleteReg(recordID + '/' + this.userinfo[0].email + '/' + this.userinfo[0].idempresa._id  + '/'
             + 'na' , 'permisos').then((data) => {
               this.pideregistros( this.currentSegment);
               }, (err) => {
                 this.authenticationService.presentAlert(err.error, this.msg, '');
             });
           }
         }
       ]
     });
     await alert.present();
   }
  async updateRecord(item: any) {
    const modal = await this.modalController.create({
      component: PermisoCreatePage,
      swipeToClose: true,
      presentingElement: this.routerOutlet.nativeEl,
      componentProps:  { variables:  this.variables, record: item, userinfo: this.userinfo, idllave: this.idllave, title: this.tituloxx}
      , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
         if ( data.data !== 'close' ) {
      this.pideregistros( this.currentSegment);
      }
    });
    return await modal.present();
}
   async  addRecord() {
    const modal = await this.modalController.create({
      component: PermisoCreatePage,
      swipeToClose: false,
      backdropDismiss: false,
     animated: true,
      presentingElement: await this.modalController.getTop(),
      componentProps:  { variables:  this.variables, record: null, userinfo: this.userinfo, idllave: this.idllave, title: this.tituloxx}
      , cssClass: 'modal-fullscreen'
    });
    modal.onWillDismiss().then((data) => {
         if ( data.data !== 'close' ) {
        this.pideregistros( this.currentSegment);
      }
    });
    return await modal.present();
   }
   loadData(event) {
    setTimeout(() => {
      event.target.complete();
      if ( this.items.length === 1000) {
        event.target.disabled = true;
      }
    }, 500);
  }
}
